#browse {
    display: -ms-flexbox;
    display: flex;
}

#browse.mobile {
    -ms-flex-direction: column;
        flex-direction: column;
}

#browse nav#browse-select {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100vh;
}

#browse.mobile nav#browse-select {
    -ms-flex-direction: row;
        flex-direction: row;
    height: auto;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

#browse nav#browse-select button{
    -ms-flex: 1 1;
        flex: 1 1;
    background: white;
    border: 1px solid black;
    color: black;
    cursor: pointer;
    -webkit-transition: all 300ms ease;
    -o-transition: all 300ms ease;
    transition: all 300ms ease;
}

#browse.mobile nav#browse-select button {
    width: 7.6%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

#browse nav#browse-select button:hover, #browse nav#browse-select button.active {
    background: black;
    color: white;
}

#browse #browse-words {
    width: 20%;
    height: 100vh;
    overflow: auto;
    padding: 0 1rem;
}

#browse.mobile #browse-words {
    width: 100%;
    max-height: 4rem;
    overflow: auto;
    padding: 0 1rem;
}

#browse #browse-words .browse-word {
    padding: 1rem;
    -webkit-transition: all 300ms ease;
    -o-transition: all 300ms ease;
    transition: all 300ms ease;
    cursor: pointer;
}

#browse #browse-words .browse-word:hover, #browse #browse-words .browse-word.active {
    background-color: black;
    color: white;
}

#browse #browse-words .browse-word:not(:first-child) {
    border-top: 1px solid black;
}

#browse #result {
    padding: 5rem;
    height: 100vh;
    overflow: auto;
}

#browse.mobile #result {
    padding: 0;
    height: auto;
}
#browse.mobile label {
    -ms-flex: 1 1;
        flex: 1 1;
}

#browse.mobile select {
    width: 100%;
    padding: 0.5rem;
    background: white;
    margin: 0.25rem 0;
}

#browse.mobile #browse-words {
    padding: 0;
    cursor: auto;
}


